.com-post-item {
  margin-bottom: 10px;
  padding: 10px;
  background: #fff;
}
.com-post-item .title,
.com-post-item .content {
  display: block;
  color: #333;
}
.com-post-item .title {
  font-weight: bold;
  margin-bottom: 10px;
}
.com-post-item .content {
  font-size: 14px;
}
.com-post-item .author-info {
  display: -ms-flexbox;
  display: flex;
  padding: 10px 0;
  line-height: 40px;
  border-bottom: 1px solid #e9e9e9;
  margin-bottom: 10px;
}
.com-post-item .author-info .name {
  margin: 0 10px;
  font-size: 16px;
}
.com-post-item .author-info .date {
  color: #999;
  font-size: 12px;
  vertical-align: baseline;
}
.com-post-item .avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}



