.com-comment-item {
  border-bottom: 1px solid #e9e9e9;
  padding: 10px 0;
}
.com-comment-item .title {
  height: 30px;
  line-height: 30px;
}
.com-comment-item .info {
  display: -ms-flexbox;
  display: flex;
}
.com-comment-item .avatar {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  border-radius: 50%;
}
.com-comment-item .nickname,
.com-comment-item .date {
  height: 30px;
  line-height: 30px;
}
.com-comment-item .date {
  margin-left: 10px;
  font-size: 12px;
  color: #999;
}
.com-comment-item .nickname {
  color: #333;
}
.com-comment-item .content {
  margin-top: 10px;
  text-indent: 40px;
}



