.com-tier-item {
  padding: 15px 0;
  border-bottom: 1px solid #e9e9e9;
}
.com-tier-item .title {
  font-weight: bold;
  margin-bottom: 5px;
  height: 30px;
  line-height: 30px;
}
.com-tier-item .content {
  margin-bottom: 15px;
  line-height: 1.5;
}
.com-tier-item .btn-join {
  width: 200px;
  height: 36px;
  line-height: 36px;
  margin: 0 auto;
  font-size: 15px;
  color: #1c4059;
  background-color: #e7ecf0;
}
.com-tier-item .btn-join::before {
  border-color: transparent !important;
}



