.page-detail {
  background: #fff;
}
.page-detail .title {
  margin-top: 20px;
  height: 30px;
  line-height: 30px;
  text-indent: 20px;
  font-size: 16px;
}
.page-detail .com-input-comment {
  width: 350px;
  margin: 0 auto;
}
.page-detail .com-input-comment .container-input-comment {
  display: -ms-flexbox;
  display: flex;
  margin-top: 20px;
}
.page-detail .com-input-comment .container-input-comment img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.page-detail .com-input-comment .container-input-comment .textarea-input-comment {
  border: 1px solid #ddd;
  margin-left: 10px;
  font-size: 12px;
  width: 280px;
}
.page-detail .com-input-comment .btn-leave-comment {
  float: right;
  width: 100px;
  height: 36px;
  line-height: 36px;
  font-size: 15px;
  margin-right: 20px;
}



