.com-brag-item {
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #e9e9e9;
  padding: 15px;
  background: #fff;
}
.com-brag-item .author-area {
  margin-right: 20px;
  text-align: center;
}
.com-brag-item .title {
  height: 30px;
  line-height: 30px;
  color: #f36838;
  font-weight: bold;
}
.com-brag-item .info {
  display: block;
  color: #333;
}
.com-brag-item .content {
  color: #555;
}
.com-brag-item .avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-bottom: 5px;
}



