.page-new .title {
  border-bottom: 1px solid #ccc;
  margin-bottom: 20px;
}
.page-new .title textarea {
  font-size: 14px;
}
.page-new .btn-submit {
  width: 350px;
  margin: 0 auto;
}
.page-new .rich-editor {
  margin-bottom: 30px;
  background: #fff;
}
.page-new .rich-editor .ql-toolbar.ql-snow {
  border-width: 0 1 1 0;
}
.page-new .rich-editor .ql-editor {
  min-height: 40vh;
}
.page-new .rich-editor .ql-editor.ql-blank::before {
  color: #ccc;
}



